import {debounce} from "@/common/utils";

export const imgLoadListerMixinByActive = {
  data() {
    return {
      refresh: null
    }
  },
  activated() {
    console.log("mixinactivated")
    //防抖,减少refresh函数调用的次数
    this.refresh = debounce(this.$refs.scroller && this.$refs.scroller.refresh, 50)
    //每加载完成一张图片就调用一次this.$refs.scroller.bscroll.refresh()以确保高度更新
    this.$bus.$on('imghasloaded', this.refresh)
  },
  deactivated() {
    console.log("mixindeactivated")
    //关闭home组件对imghasloaded事件的监听
    this.$bus.$off('imghasloaded', this.refresh)
  }
}
export const imgLoadListerMixinByMounted = {
  data() {
    return {
      refresh: null
    }
  },
  mounted() {
    console.log("mixinmounted")
    //防抖,减少refresh函数调用的次数
    this.refresh = debounce(this.$refs.scroller && this.$refs.scroller.refresh, 50)
    //每加载完成一张图片就调用一次this.$refs.scroller.bscroll.refresh()以确保高度更新
    this.$bus.$on('imghasloaded', this.refresh)
  },
  destroyed() {
    console.log("mixindestroyed")
    //关闭home组件对imghasloaded事件的监听
    this.$bus.$off('imghasloaded', this.refresh)
  }
}
//混入函数回到顶部
import ScrollBack from "@/components/content/scrollback/ScrollBack";
export const BackTopMixin = {
  components: {
    ScrollBack
  },
  data() {
    return {
      isbackshow: false,
    }
  },
  methods: {
    //  回到顶部
    backToTop() {
      this.$refs.scroller.scrollTo(0, 0)
    },
    //是否显示回到顶部
    IsBackTopshow(position) {
      if (-position.y > 1000){
        this.isbackshow = true
      }else {
        this.isbackshow = false
      }
    }
  }

}
