<template>
  <div class="goods-list-item" @click="ShowDetials">
    <a href="javascript:;" >
      <img v-lazy="showimg" alt="" @load="imgLoaded">
      <div class="fonts">
        <p><i class="iconfont">&#xe600;</i>{{goodsitem.title}}</p>
        <span>
          <i class="iconfont">&#xe66a;</i>{{goodsitem.price}}
          <i class="iconfont">&#xe60a;</i>{{goodsitem.sale}}
        </span>
      </div>
    </a>
  </div>
</template>

<script>
  export default {
    name: "goodsListItem",
    props: {
      goodsitem: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed:{
      showimg(){
        return this.goodsitem.image || this.goodsitem.show.img
      }
    },
    methods:{
      imgLoaded(){
        this.$bus.$emit('imghasloaded')
      },
      ShowDetials(){
        this.$router.push('/detail/' + this.goodsitem.iid)
      }
    },
  }
</script>

